import { NavLink } from "react-router-dom";
import "./Table.css"
function Items(props) {

  return (
    <div >
   
      <div className="itemBox d-flex bg-dark text-white " id={props.key}>
      <div className="img-box col-1">
      <img
       
          src={props.image}
          alt="auto"
          style={{
            width: "1.5rem",
            height:"1.5rem",
          }}
        />
      </div>

     <div className="col-1">
     <h1  >{props.symbol}</h1>

     </div>


     <div className="col-1">
     <NavLink
          
          exact to={`/table/buy/${props.Name}`}
          className="name"
          state={{
            id: props.key,
            name: props.Name,
            price: props.price,
            symbol: props.symbol,
            image: props.image,
            check:props.check
          }}
        >
         
     <h1 className="name ">{props.Name}</h1>
         
        </NavLink>

     </div>

     <div  className="col-2">
     <h1    style={{color:props.low24<props.price?"green":"red"}}>{props.price}</h1>

     </div>

     <div  className="col-2">
     <h1 className="change ">{Math.ceil(props.changePercent)}</h1>

     </div>

     <div  className="col-2">
     <h1 className="high ">{props.high24}</h1>

     </div>
     
     <div  className="col-2 low">
     <h1 >{props.low24}</h1>

     </div>
      </div>
    </div>
  );
}

export default Items;
